.checkbox {
  opacity: 0;
  position: absolute;
  width: 0;
}

.label {
  border-radius: 50px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
  position: relative;
  top: 100px;
  left: 95%;
  height: 18px;
  width: 40px;
  transform: scale(1.5);
}

.label .ball {
  background-color: #eeca5f;
  border-radius: 50%;
  position: absolute;
  top: 2px;
  left: 2px;
  height: 14px;
  width: 15px;
  transform: translateX(0px);
  transition: transform 0.3s linear;
}

.checkbox:checked + .label .ball {
  transform: translateX(20px);
}
