.contactMe {
  padding: 100px 50px;
  font-family: "Raleway", sans-serif;
  text-align: center;
}

.contact input,
textarea {
  padding: 10px;
  margin: 15px;
  width: 70%;
}
.inputContainer {
  position: relative;
  margin-bottom: 25px;
}
.inputContainer label {
  position: absolute;
  top: 0px;
  left: 15%;
  font-size: 16px;
  color: #fff;
  transition: all 0.5s ease-in-out;
}
.inputContainer label {
  color: blueviolet;
}

.inputContainer input,
textarea {
  border: 0;
  border-bottom: 1px solid #555;
  background: transparent;
  width: 70%;
  padding: 8px 0 5px 0;
  font-size: 16px;
  color: #fa7d09;
}
.inputContainer input:focus,
textarea:focus {
  border: none;
  outline: none;
  border-bottom: 1px solid blueviolet;
}

.inputContainer input:focus ~ label,
.inputContainer input:valid ~ label {
  top: -20px;
  left: 10px;
  font-size: 15px;
}
.inputContainer textarea:focus ~ label,
.inputContainer textarea:valid ~ label {
  top: -20px;
  left: 10px;
  font-size: 15px;
}

.getinTouch h3 {
  padding: 50px 0 20px;
  font-weight: 900;
  font-size: 2rem;
}

.getinTouch a {
  text-decoration: none;
  font-size: 1.5rem;
}

.getinTouch a:hover {
  color: #fff;
}

.getinTouch .details {
  width: 190px;
  padding: 10px;
  margin: auto;
  border-radius: 20px;
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.getinTouch .details:hover {
  background-color: #a05344;
  border-radius: 10px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  transform: scale(1.2);
}

.getinTouch .icon {
  font-size: 4.5rem;
  color: #52057b;
  padding-bottom: 10px;
}
