@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@300&family=Roboto:wght@500&display=swap");

.abtme {
  padding: 100px 50px;
  font-family: "Raleway", sans-serif;
  text-align: center;
  line-height: 2;
}
.title {
  padding-bottom: 20px;
  font-family: "Roboto", sans-serif;
}
.colleft {
  animation: leftToRight 3s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

.colright {
  animation: rightToLeft 3s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

.myimage {
  margin-top: 20px;
  width: 100%;
  border-radius: 20px;
  object-fit: cover;
  filter: grayscale(70%);
  transition: 0.3s ease-in-out;
  animation: leftToRight 3s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

@keyframes leftToRight {
  0% {
    transform: translateX(-70%);
    opacity: 0;
  }

  100% {
    transform: translateX(0%);
    opacity: 1;
  }
}

@keyframes rightToLeft {
  0% {
    transform: translateX(70%);
    opacity: 0;
  }

  100% {
    transform: translateX(0%);
    opacity: 1;
  }
}

.myimage:hover {
  filter: grayscale(0);
}
.CvDownload {
  background-color: #082032;

  border: none;
  padding: 10px;
  font-size: 1.3rem;
  font-weight: bold;
  border-radius: 3px;
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}
.CvDownload a {
  color: #ff4848;
  text-decoration: none;
}

.CvDownload a:hover {
  color: white;
  text-decoration: none;
}

.CvDownload:hover {
  color: white;
  border-radius: 10px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  transform: scale(1.1);
}

@media (min-width: 900px) and (max-width: 2000px) {
  .myimage {
    width: 300px;
  }
}

@media (max-width: 767px) {
  .myimage {
    width: 70%;
  }
}
