.next:hover {
  animation: rotation 2s infinite linear;
}

@keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
  }
}

.icon:hover {
  box-shadow: 7px 1px 15px 2px rgba(0, 0, 0, 0.44);
  -webkit-box-shadow: 7px 1px 15px 2px rgba(0, 0, 0, 0.44);
  -moz-box-shadow: 7px 1px 15px 2px rgba(0, 0, 0, 0.44);
}
