@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@300&family=Roboto:wght@500&display=swap");

.home {
  padding: 0px 50px;
  padding-bottom: 40px;
  text-align: center;
}
.jumbo {
  padding: 100px 0;
  font-family: "Roboto", sans-serif;
}

.title {
  font-weight: 300;
  line-height: 1.2;
  animation: rightToLeft 3s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

.lead {
  font-size: 1.3rem;
  font-family: "Raleway", sans-serif;
}
.imageContainer {
  box-shadow: 7px 1px 15px 2px rgba(0, 0, 0, 0.44);
  -webkit-box-shadow: 7px 1px 15px 2px rgba(0, 0, 0, 0.44);
  -moz-box-shadow: 7px 1px 15px 2px rgba(0, 0, 0, 0.44);
  border-radius: 20px;
  width: 40%;
  animation: leftToRight 3s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}
@media (max-width: 950px) {
  .imageContainer {
    width: 60%;
  }
}
@keyframes leftToRight {
  0% {
    transform: translateX(-70%);
    opacity: 0;
  }

  100% {
    transform: translateX(0%);
    opacity: 1;
  }
}

@keyframes rightToLeft {
  0% {
    transform: translateX(70%);
    opacity: 0;
  }

  100% {
    transform: translateX(0%);
    opacity: 1;
  }
}

/* for 3 div boxes that zoom out */
.box {
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.box:hover {
  border-radius: 10px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  animation-name: example;
  animation-duration: 0.25s;
  transform: scale(1.1);
  border-left: 3px solid rgb(0, 0, 0);
  border-right: 3px solid rgb(0, 0, 0);
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

@keyframes example {
  0% {
    border-left: 1px solid #ffffff;
    border-right: 1px solid #ffffff;
  }
  25% {
    border-left: 1px solid #ffe6e6;
    border-right: 1px solid #ffe6e6;
  }
  50% {
    border-left: 1px solid #363636;
    border-right: 1px solid #363636;
  }
  100% {
    border-left: 1px solid #000000;
    border-right: 1px solid #000000;
  }
}
